* {
    box-sizing: border-box;
    margin-top: 0;
    padding: 0px;
    font-family: Arial, Helvetica, sans-serif;
}

.profile-cards {
    margin: 25px;
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-around;
    position: relative;
    height: 400px;
    overflow: visible;
    scroll-behavior: smooth;
    overflow-x: auto;
}

.card {
    height: 350px;
    width: 350px;
    border-radius: 2rem;
    overflow: hidden;
    margin: 0 7.5px;
    display: flex;
    flex-direction: column;
    align-items: center;
    transition: transform 0.6s cubic-bezier(.28,-0.03,0,.99);
    box-shadow: 0px 5px 5px 2.5px rgba(0, 0, 0, 0.725);
    opacity: 0;
    transform: translateX(100%);
    animation: slideIn 0.8s ease forwards;
}

@keyframes slideIn {
    0% {
        transform: translateX(100%);
        opacity: 0;
    }
    100% {
        transform: translateX(0);
        opacity: 1;
    }
}

.card > .row {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
}

.card img {
    width: 100%;
    height: 350px;
    object-fit: cover;
    border-radius: 2rem;
    cursor: pointer;
}

.description {
    color: rgb(255, 255, 255);
    display: flex;
    align-items: center;
    flex-direction: column;
    opacity: 0;
    transition: opacity 0.3s ease;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 10px;
    background: rgba(0, 0, 0, 0.5);
    height: 50px;
}

.description h4 {
    font-family: 'Major Mono Display', monospace; 
    margin: 0;
}

.card:hover .description {
    opacity: 1;
}


h2 {
    font-family: 'Righteous', sans-serif;
    font-size: 2.5em;
    color: #333;
    margin: 25px 50px;
}


.slide-enter {
    animation: slideIn 300ms forwards;
}

.slide-exit {
    animation: slideIn 300ms forwards;
    transform: translateX(0);
    opacity: 1;
}