.contact-us {
    padding: 20px;
    margin: 15px auto;
    text-align: center;
    max-width: 600px;
    margin-top: -175px;
}

.contact-title {
    font-family: 'Righteous', sans-serif;
    font-size: 2em;
    color: #333;
    margin-bottom: 15px;
}

.contact-info {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.contact-item {
    display: flex;
    align-items: center;
    margin: 10px 0;
}

.contact-icon {
    margin-right: 10px;
    width: 24px;
    height: 24px;
}

.contact-text {
    font-family: 'Major Mono Display', monospace;
    font-size: 1.1em;
    font-weight: bold;
    color: #555;
}
