.about-us {
    padding: 20px;
    margin: 50px;
    background-color: #f9f9f9;
    border-radius: 10px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.5);
}

.about-title {
    font-family: 'Righteous', sans-serif;
    font-size: 2em;
    color: #333;
    margin-bottom: 15px;
    margin-top: 15px;
}

.about-description {
    font-family: 'Major Mono Display', monospace;
    font-size: 1em;
    color: #555;
    line-height: 1.6;
    font-weight: bold;
}

.divider-container {
    display: flex;            
    justify-content: center;  
    margin: 50px 0;          
}

.divider {
    width: 750px;            
    border: none;            
    height: 2.5px;          
    background-color: #555;  
}
